.repo {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #c2c2c2;
  max-width: 600px;
  margin: 20px auto;
}
.repo .title {
  background: #f5f5f5;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.repo .title h3 {
  margin: 0 auto;
  padding: 5px;
}
.repo .content {
  background: white;
  padding: 10px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
