.itemsList h4 {
  margin-bottom: 0;
  padding: 0;
}
.itemsList ul {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  list-style-type: none;
  margin: auto;
  padding: 0;
  max-width: 400px;
  margin: 5px auto 10px;
  border: 1px solid transparent;
}
.itemsList ul li {
  background: #fafafa;
}
.itemsList ul li:nth-of-type(2n) {
  background: #f5f5f5;
}
